<template>
 <b-row>
   <b-col cols="5">
     <b-form-select :value="type" :options="options" class="mb-3" @change="val=>$emit('update:type', val)">
     </b-form-select>

   </b-col>
   <b-col cols="1">

   </b-col >
   <b-col cols="5">
     <b-form-input :value="amount"
                   type="text"
                   @input="val=>$emit('update:amount',val)"
                   placeholder="Amount"></b-form-input>
   </b-col>
 </b-row>

</template>

<script>
    export default {
      name: '',
      props: ['options', 'amount', 'type']
    }
</script>

<style scoped>

</style>



