<template>
<div>

  <div v-for="offer in offers">
    <h1>{{offer.Title}}</h1>
    <!--<div v-for="app in offer.Applications">-->
      <!--<h4>{{app.ApplicantId}}</h4>-->
    <!--</div>-->
    {{offer.Applications.length}}
    <b-btn to="supplier/offers/2/applications" variant="primary">Applicants</b-btn>
  </div>

  <div>
    <b-btn v-b-modal.modal1>Add Offer</b-btn>
      <new-offer></new-offer>

  </div>
</div>
</template>

<script>
  import {mapGetters} from 'vuex'
  import NewOffer from './new-offer'
  export default {
    name: '',
    computed: {
      ...mapGetters({
        offers: '$_supplier_view/offers'
      })
    },
    components: {
      NewOffer
    }
}
</script>

<style scoped>

</style>



