<template>
  <b-modal ok-title="Add"  @ok="addOffer" size="lg" :no-close-on-backdrop="true" id="modal1" title="New Offer">
  <div>
    <b-row>
      <b-col>
        <b-row>
          <b-col>
            <b-card  img-src="static/img/avatars/3.jpg"></b-card>

          </b-col>
          <b-col>
            <b-form-input v-model="newOffer.Title"
                          type="text"
                          placeholder="Title"></b-form-input>

          </b-col>
        </b-row>

      </b-col>
      <b-col>
        <p>From: <strong> {{newOffer.StartDate| moment("dddd, MMMM Do YYYY")}} </strong></p>
        <p>To: <strong> {{newOffer.EndDate| moment("dddd, MMMM Do YYYY")}} </strong></p>
        <date-picker v-model="dateRange" range :shortcuts="shortcuts" lang="en"></date-picker>

      </b-col>
    </b-row>

    <b-row style="margin-top: 20px">
      <b-col>
        <b-card title="Description">
        <b-form-textarea id="textarea1"
                         v-model="newOffer.Description"
                         placeholder="Description"
                         :rows="3"
                         :max-rows="6">
        </b-form-textarea>
        </b-card>
      </b-col>
      <b-col>
        <b-card title="Requirements">
        <requirement-list :items="newOffer.Requirements"></requirement-list>
        </b-card>
      </b-col>
    </b-row>

  </div>
  </b-modal>
</template>

<script>
  import DatePicker from 'vue2-datepicker'
  import Offer from '../../offer'
  import RequirementList from './Requirements/RequirementList'
  // import {mapState} from 'vuex'
  export default {
    name: '',
    components: { DatePicker, RequirementList },
    data () {
      return {
        newOffer: new Offer(),
        shortcuts: [
          {
            text: 'Today',
            start: new Date(),
            end: new Date()
          }
        ]
      }
    },
    computed: {
    //  ...mapState('$_supplier_view', ['newOffer'])
      dateRange: {
        get: function () {
          const arr = []
          return arr.push(this.newOffer.StartDate, this.newOffer.EndDate)
        },
        set: function (arr) {
          this.newOffer.StartDate = arr[0]
          this.newOffer.EndDate = arr[1]
        }
      }
    },
    methods: {
      addOffer () {
        console.log(this.newOffer)
        this.$store.dispatch('$_supplier_view/SAVE_OFFER', this.newOffer).then(() => {
          console.log('reset')
          this.newOffer = new Offer()
        })
      }
    }
  }
</script>

<style scoped>

</style>



